@import "utilities.scss";

.App {
  overflow: hidden;
  background: url("../assets/noise.png") 0% 0% / 10%,
    // linear-gradient(130deg, rgba(166, 163, 159, 1) 0%, rgb(107, 106, 103) 100%),
      radial-gradient(
        40.8% 68.67% at 50% 54.89%,
        rgba(166, 163, 159, 1) 0%,
        rgb(136, 135, 131) 100%
      );
}
body {
  margin: 0;
  font-size: 2.3vh;
  font-family: ff-scala-sans-pro, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

