.place-tag,
.load-video {
  height: 100%;
}

.place-tag-animation,
.load-video-animation {
  position: relative;
  .animation-box,
  .animation-tag,
  .animation-player {
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    height: 100%;
  }
  .animation-box {
    width: 70%;
    max-width: 500px;
    background: url("../assets/box@2x.png") 50% 80% / contain no-repeat;
  }
  .animation-tag {
    width: 25%;
    max-width: 140px;
    background: url("../assets/tag@2x.png") 50% 10% / contain no-repeat;
  }
  .animation-player {
    width: 70%;
    background: url("../assets/player-c@2x.png") 50% 80% / contain no-repeat;
  }
}

.headline {
  font-size: 1.5em;
  color: #fff;
  position: relative;
  p {
    padding: 0 20px;
    width: 100%;
    position: absolute;
    bottom: 0;
    text-align: center;
  }
}
.load-video p {
  top: 0;
}
